///////////////////////////
///// Variables /////
///////////////////////////
@mixin transition($args...) {
	-webkit-transition: $args;
	-moz-transition: $args;
	-ms-transition: $args;
	-o-transition: $args;
	transition: $args;
}

///////////////////////////
///// Values /////
///////////////////////////
.menu {
	opacity: 0;
	position: absolute;
	height: 20rem;
	width: 30rem;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	border: 0.3rem solid rgb(103, 0, 131);
	border-radius: 5%;
	background-color: rgb(36, 36, 36);
	font-family: 'Rhodium Libre', serif;
	@include transition(opacity 1s ease-in-out);
	.wut {
		position: absolute;
		top: 2%;
		left: 5%;
		color: white;
	}
	.exit {
		position: absolute;
		width: 2rem;
		top: 2%;
		right: 2%;
		fill: white;
	}
	.save {
		position: absolute;
		top: 84%;
		left: 12%;
	}
	.colorPicker {
		position: absolute;
		height: 65%;
		width: 45%;
		top: 50%;
		left: 25%;
		transform: translate(-50%, -50%);
