///////////////////////////
///// Variables /////
///////////////////////////
@mixin transition($args...) {
	-webkit-transition: $args;
	-moz-transition: $args;
	-ms-transition: $args;
	-o-transition: $args;
	transition: $args;
}

///////////////////////////
///// Values /////
///////////////////////////
.hud {
	opacity: 0;
	position: absolute;
	height: 4%;
	width: 15%;
	bottom: 0%;
	left: 1%;
	@include transition(opacity 1s ease-in-out);
	.container {
		position: relative;
		width: 3rem;
		.voiceCover {
			position: absolute;
			height: 75%;
			width: 75%;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: rgb(0, 0, 0);
			border-radius: 50%;
			.voiceIcon {
				position: absolute;
				height: 65%;
