///////////////////////////
///// Main Window /////
///////////////////////////
.mainWindow {
	width: 100%;
	min-height: 100vh;
}

@media (min-width: 1280px) and (min-height: 720px) {
	html {
		font-size: 11px;
	}
}
@media (min-width: 1920px) and (min-height: 1080px) {
	html {
		font-size: 16px;
	}
}
@media (min-width: 2560px) and (min-height: 1440px) {
	html {
		font-size: 22px;
	}
}
@media (min-width: 3840px) and (min-height: 2160px) {
	html {
		font-size: 33px;
	}
}
